.bar {
  height: 60px;
  width: 100%;
  align-items: center;
  position: fixed;
  background-color: rgb(210, 246, 255);
  top: 0;
  z-index: 2;
}
.bar .allURL {
  width: 65%;
  margin: 0px auto;
  display: grid;
  grid-template-columns: 16.6% 16.6% 16.6% 16.6% 16.6% 16.6%;
}

.bar a {
  display: flex;
  justify-content: center;
  align-items: center;
}

.bar .divfalse {
  height: 60px;
  width: 100%;
  font-size: 100%;
  background-color: rgb(228, 228, 228);
  font-weight: bold;
  border: none;
  margin: 0 auto;
  text-decoration: none;
}

.bar .divtrue:hover,
.bar .divfalse:hover {
  background-color: rgb(170, 45, 45);
  color: white;
}
.bar .divtrue:focus,
.bar .divfalse:focus {
  background-color: rgb(0, 136, 61);
  color: white;
}
.bar .divtrue:active,
.bar .divfalse:active {
  background-color: rgb(0, 136, 61);
  color: white;
}

.bar a:-webkit-any-link {
  color: black;
}

.bar .divtrue {
  color: white !important;
  text-decoration: none;
  width: 100%;
  font-size: 100%;
  background-color: rgb(36, 141, 5);
  font-weight: bold;
  border: none;
  margin: 0 auto;
}

@media only screen and (max-width: 999px) {
  .bar {
    top: 0px;
    height: 50px;
    width: 100%;
    align-items: center;
    position: fixed;
    background-color: rgb(228, 228, 228);
    z-index: 2;
  }

  .bar .allURL {
    height: 50px;
    width: 100%;
    margin: 0px auto;
    text-align: center;
    display: grid;
    grid-template-columns: 20% 20% 20% 20% 20%;
  }

  .bar .divfalse {
    height: 100%;
    width: 90%;
    font-size: 12px;
    font-weight: bold;
    border: none;
    margin: 0 auto;
  }

  .bar .divtrue:hover,
  .bar .divfalse:hover {
    border-radius: 0px;
    background-color: rgb(114, 114, 114);
    color: white;
  }

  .bar .divtrue {
    color: white !important;
    border-radius: 0px;
    width: 100%;
    height: 100%;
    font-size: 12px;
    background-color: rgb(114, 114, 114);
    border: none;
    margin: 0 auto;
  }
}
